___CSS_LOADER_EXPORT___.push([module.id, `/** background:#4D7C8A; **/

:root {
  --text-color: #23424b;
}

body {
  margin: 0;
  min-height: 690px;
  min-width: 650px;

  align-content: center;

  background-color: #ffffff;
  background-image:
    linear-gradient(90deg, #ccf0ff80 40px, transparent 40px),
    linear-gradient(#ccf0ff80 40px, transparent 40px);
  background-position: 10px 10px;
  background-size: 78px 78px;
}

h1 {
  color: var(--text-color);
    font-family: "Baskerville", "Georgia", serif;
    font-style: italic;
    font-size: 5em !important;
    margin: 0;
}

h2 {
    font-size: 1.5em !important;
    color: var(--text-color);
    margin: 0;
}

.container{
  display: flex;
  direction: column;
  gap: 5px;
}

.footer {
  height:100px;
  width: 100vw;
  background-color:var(--text-color);
  align-items: center;
}


/* Create page layout */
.create-page-layout {
    display: grid;
    grid-template-columns: 60px 2fr 1fr;
    grid-template-rows: 60px 1fr;
    gap: 4px 6px;
    grid-template-areas:
    "nav-bar toolbar toolbar"
    "nav-bar page-drawing page-list";
}


.nav-bar {
    grid-area: nav-bar;
}

.page-drawing {
    grid-area: page-drawing;
    background-color: var(--snow);
}

.page-list {
    grid-area: page-list;
    background-color: var(--snow);
    overflow-y: scroll
}

.toolbar{
    grid-area: toolbar;
    background-color: var(--snow);
}


